import { Link } from "react-router-dom";
import React from "react";
import CSSTransitionGroup from "react-addons-css-transition-group";
import { increment } from '../redux/actions/actionCreators'
import { connect } from 'react-redux';

const Photo = props => {

    const onIncrementLike = i => {
        increment(i);
      };

    const { post, i, comments, increment } = props;

    return (
        <figure className="grid-figure">
          <div className="grid-photo-wrap">
            <Link to={`/view/${post.code}`}>
              <img
                src={post.display_src}
                alt={post.caption}
                className="grid-photo"
              />
            </Link>
    
            <CSSTransitionGroup
              transitionName="like"
              transitionEnterTimeout={500}
              transitionLeaveTimeout={500}
            >
            <span key={post.likes} className="likes-heart">
            {post.likes}
          </span>
        </CSSTransitionGroup>
        </div>
      <figcaption>
        <p>{post.caption}</p>
        <div className="control-buttons">
          <button onClick={() => onIncrementLike(i)} className="likes">
            &hearts; {post.likes}
          </button>
          <Link className="button" to={`/view/${post.code}`}>
            <span className="comment-count">
              <span className="speech-bubble"> </span>
              {comments[post.code] ? " " + comments[post.code].length : " " + 0}
            </span>
          </Link>
        </div>
      </figcaption>
    </figure>
    );
}

const mapStateToProps = state => ({
    comments : state.comments
})

export default connect(mapStateToProps, { increment })(Photo);